import style from '../assets/index.css';
import { useNavigation } from '@react-navigation/native';
import $ from 'jquery'

function SideNavi(name) {
	return (
		<li key={name}>
			<a href={"#" + name} id={name + "Navi"}>
				<span>
					{name.replace("-", " ")}
				</span>
			</a>
		</li>
	);
}

function ToolBar() {
	return (
		<>
			<h3>Tools</h3>
			<ul>
				<li><a onClick={() => $('.fold-block').show()} style={{cursor: "pointer"}}>Expand all</a></li>
				<li><a onClick={() => $('.fold-block').hide()} style={{cursor: "pointer"}}>Fold all</a></li>
			</ul>
		</>
	);
}

function SideBar({sections, tool = false}) {
	const navigation = useNavigation();
	var navis = [];
	for (var i = 0; i < sections.length; i ++) {
		navis.push(SideNavi(sections[i]));
	}
	return (
		<aside className="sidebar" id="sidebar">
	        {/*<a href="#" onclick="javascript:foldSideBar(); return false;">
	          <div id="fold"><img src="./index_files/assets/images/fold.png" id="foldpng"></div></a>*/}
	        <div id="sidecontent">
		        <h3>
		            <span lang="en">Contents</span>
		        </h3>
		        <ul id="side1">
	          		{navis}
	        	</ul>
	        	{tool && <ToolBar />}
        	</div>
      	</aside>
	);
}

export default SideBar;